// components/QuoteModal.tsx
"use client";

import { Dialog, DialogTrigger, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import ContactForm from "./Quote";

export default function QuoteModal() {
  return (
    <Dialog>
      <DialogTrigger asChild>
        <Button className="bg-blue-600 hover:bg-blue-700 text-white px-8 py-4 rounded-xl font-semibold transition-all duration-300 shadow-lg hover:shadow-xl transform hover:-translate-y-1">
          Get a Quote
        </Button>
      </DialogTrigger>
      <DialogContent className="max-w-lg sm:w-full rounded-2xl border-0 shadow-2xl">
        <DialogHeader className="text-center">
          <DialogTitle className="text-2xl font-bold text-gray-900">Get Your Custom Quote</DialogTitle>
          <DialogDescription className="text-gray-600">
            Fill out the form below to get a custom quote for your transportation needs. We'll get back to you within 24 hours.
          </DialogDescription>
        </DialogHeader>
        <ContactForm />
      </DialogContent>
    </Dialog>
  );
}